import React, { FC, FormEvent, useEffect } from "react";
import styles from "./form.module.css";
import {
  Button,
  Input,
  PasswordInput,
} from "@ya.praktikum/react-developer-burger-ui-components";
import useFormAndValidation from "../hooks/useFormAndValidation";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { useSelector, useDispatch } from "../services/hooks";
import { login } from "../services/actions/actionsAuth";

export const Login: FC = () => {
  const { values, handleChange, isValid, setValues } =
    useFormAndValidation();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { email, password } = values;
  const location = useLocation();
  const { accessToken } = useSelector((state) => state?.auth);
  useEffect(() => {
    location.state
      // @ts-ignore
      ? accessToken && navigate(location.state.from)
      : accessToken && navigate("/");
  }, [accessToken]);

  const handleSubmit = (e: FormEvent): void => {
    e.preventDefault();
    isValid && handleLogin({ email, password });
    setValues({});
  };
  // @ts-ignore
  const handleLogin = ({ email, password }) => {
    dispatch(login(email, password));
  };

  return (
    <div className={styles.wrap}>
      <form className={styles.form} onSubmit={handleSubmit} name="login-form">
        <h1 className="text text_type_main-medium mb-6"></h1>
        <div className={`mb-6 ${styles.inputWrap}`}>
          <Input
            type={"email"}
            placeholder={"E-mail"}
            value={email || ""}
            name={"email"}
            error={false}
            errorText={"  "}
            onChange={handleChange}
          />
        </div>
        <div className={`mb-6 ${styles.inputWrap}`}>
          <PasswordInput
            value={password || ""}
            name={"password"}
            size={"default"}
            onChange={handleChange}
          />
        </div>
        <div className="mb-20">
          <Button type="primary" size="medium">
            
          </Button>
        </div>
        <p className="text text_type_main-default text_color_inactive mb-4">
             ?{" "}
          <Link to={"/register"} className={styles.link}>
            
          </Link>
        </p>
        <p className="text text_type_main-default text_color_inactive">
           ?{" "}
          <Link to={"/forgot-password"} className={styles.link}>
             
          </Link>
        </p>
      </form>
    </div>
  );
};
